import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import persona from '@/components/Persona'
import muelte from '@/components/Muelte'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/Persona',
      name: 'Persona',
      component: persona,
      children: [
        {
          path: 'Muelte',
          component: muelte,
          props: {infopasada: true}
        }
      ]
    }
  ]
})



