<template>
  <div>
    <article v-for="(localizacion, idx) in localizaciones" :key="idx">
      <h1> punto: {{ localizacion.nombre }} </h1>
    </article>
    <button @click="volverHello">Ir a hello</button>
    <template v-if="persona">
      <h1 v-text="datosPersona.nombre"></h1>
      <h2 v-text="datosPersona.correoe"></h2>
      <img :src="datosPersona.foto">
    </template>
    <slot v-else>Cargando...</slot>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'
import { db } from '../main'

export default {
  mounted () {
    setTimeout(() => {
      axios.get('https://randomuser.me/api/').then((respuesta) => {
        this.persona = respuesta.data.results[0]
      })
    }, 2000)
  },
  data () {
    return {
      persona: null,
      localizaciones: []
    }
  },
  computed: {
    datosPersona () {
      return {
        nombre: this.persona.name.first,
        foto: this.persona.picture.large,
        correoe: this.persona.email
      }
    }
  },
  methods: {
    volverHello () {
      this.$router.push('/HelloWorld')
    }
  },
  firestore () {
    return {
      localizaciones: db.collection('localizaciones').orderBy('creado')
    }
  }
}
</script>

<style>
</style>



