// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueFire from 'vueFire'
import firebase from 'firebase'
import 'firebase/firestore'

Vue.config.productionTip = false

Vue.use(VueFire)

var config = {
  apiKey: 'AIzaSyBoczTglheLBGd-q10K8PMN9gC5fJvwisQ',
  authDomain: 'quimera-metric.firebaseapp.com',
  databaseURL: 'https://quimera-metric.firebaseio.com',
  projectId: 'quimera-metric',
  storageBucket: 'quimera-metric.appspot.com',
  messagingSenderId: '1001339403171'
}
firebase.initializeApp(config)

export const db = firebase.firestore()
const settings = {timestampsInSnapshots: true}
db.settings(settings)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



